import { Component, OnInit } from '@angular/core';
import { DCore_Http, DCore_Page, DCore_Valid } from '../../typescript/dcem.core';
import { ModalController, IonContent, NavParams } from '@ionic/angular';
import * as $ from 'jquery';
import { Storage_LoginInfo } from '../../typescript/logininfo.storage';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  public tab: any = 'account';
  public title: any = '';
  public isval: any = false;//
  public valmsg: any = '';//
  // 1 25 3 4  
  //6 //7 
  public disstatus: any = 7;
  public mod: any = {
    loginurl: 'api/user/loginaccount',//url
    loginphoneurl: 'api/user/loginphone',//url
    regurl: 'api/user/adduser',//url 
    sendmsgurl: 'api/user/sendmsg',//url 
    questionsurl: 'api/user/getquestions',//url 
    getuserurl: 'api/user/getuser',
    resetpwdurl: 'api/user/resetpwd',//
    questiondata: [],
    val1: '',
    val2: '',
    val3: '',
    val4: '',
    model: {
      account: '',//
      pwd: '',// 
      name: '',//
      sex: '',
      msg: '',
      pwd1: '',
      quest1: '',
      quest2: '',
      quest3: '',
      quest1value: '',
      quest2value: '',
      quest3value: ''
    }, loginaccount: {
      code: '',
      name: '',
      nickname: '',
      gender: '',
      phone: '',
      cardid: '',
      email: '',
      birthday: '',
      marriagestatus: '',
      profession: '',
      company: '',
      signature: '',
      description: '',
      memberid: ''
    }
  }
  constructor(
    private modalCtrl: ModalController,
    private _http: DCore_Http,
    private _logininfo: Storage_LoginInfo,
    private _page: DCore_Page,
    private _valid: DCore_Valid,
    private _navParams: NavParams) {
    this.disstatus = _navParams.get('status')
    this.OnQuests();
  }

  //
  OnLogin() {
    this.disstatus = 1;
  }

  OnPwdReset() {
    this.title = '';
    this.disstatus = 6;
  }
  //
  onReturn(islogin) {
    this.modalCtrl.dismiss({
      'login': islogin
    });
  }

  //
  OnQuests() {
    var postData = {

    };
    this._http.get(
      this.mod.questionsurl,
      postData,
      (res: any) => {
        if (res.Result == true) {
          for (var key in res.Data) {
            var obj = {};
            obj = res.Data[key]["Attributes"];
            this.mod.questiondata.push(obj);
          }
        }
      },
      (err: any) => {
        this._page.alert("", "");
      }
    );
  }

  //
  onSendMsg(status) {
    if (this.mod.model.account == '') {
      this._page.alert("", "");
      return false;
    }
    if (String(this.mod.model.account).length != 11) {
      this._page.alert("", "");
      return false;
    }
    this._page.loadingShow();
    var postData = {
      phone: this.mod.model.account,
      type: (status == "2" ? 2 : (status == "5" ? 1 : (status == "3" ? 3 : 4))),
      valcode: ""
    };
    this._http.post(
      this.mod.sendmsgurl,
      postData,
      (res: any) => {
        if (res.Result == true) {
          //
          if (status == 2 || status == 5)
            this.disstatus = status;
          this._page.loadingHide();
        } else {
          this._page.loadingHide();
          this._page.alert("", res.Description);
        }
      },
      (err: any) => {
        this._page.loadingHide();
        this._page.alert("", "");
      }
    );
  }

  public i: any = 0;
  //
  OnVlaCodeChange() {
    this.i++;
    if (this.i != 1)
      return false;
    this._page.loadingShow();
    var valcode = String(this.mod.val1) + String(this.mod.val2) + String(this.mod.val3) + String(this.mod.val4);
    var postData = {
      account: this.mod.model.account,
      valcode: valcode,
      logintype: 1,
      type: String(this.disstatus),
      keytype: 1,
      status: 2,
      pwd: this.mod.model.pwd
    };

    this._http.post(
      this.mod.loginphoneurl,
      postData,
      (res: any) => {
        this._page.loadingHide();
        if (res.Result == true) {
          //
          if (this.disstatus == 5)
            this.disstatus = 4;
          else {
            this.LoginModel(res.Data);
            this.onReturn(true);
          }
        }
        else {
          this.mod.val1 = "";
          this.mod.val2 = "";
          this.mod.val3 = "";
          this.mod.val4 = ""; 
          this.i = 0;
          this._page.alert("", res.Description);
        }
      },
      (err: any) => {
        this.i = 0;
        this._page.loadingHide();
        this._page.alert("", "");
      }
    );

  }



  OnNextFocus(code) {
    var inputval = '#inputcode' + code;
    $(inputval).focus();
  }
  //
  onLogin() {
    this._page.loadingShow();
    var postData = {
      account: this.mod.model.account,
      pwd: this.mod.model.pwd,
      keytype: 1,
      logintype: 1,
      status: 2,
      certificationtype: 1,
      valcode: "",
      type: ""
    };
    this._http.post(
      this.mod.loginurl,
      postData,
      (res: any) => {
        if (res.Result == true) {
          this.LoginModel(res.Data);
          this.onReturn(true);
        }
        else {
          this._page.alert("", ",");
        }
        this._page.loadingHide();
      },
      (err: any) => {
        this._page.loadingHide();
        this._page.alert("", "");
      }
    );
  }

  LoginModel(data: any) {
    this.mod.loginaccount.account = data["Attributes"]["account"];
    this.mod.loginaccount.systemuserid = data["Attributes"]["mcs_userid"];
    this.mod.loginaccount.code = data["Attributes"]["mcs_code"];
    this.mod.loginaccount.name = data["Attributes"]["mcs_name"];
    this.mod.loginaccount.nickname = data["Attributes"]["mcs_nickname"];
    this.mod.loginaccount.gender = data["Attributes"]["mcs_gender@OData.Community.Display.V1.FormattedValue"];
    this.mod.loginaccount.phone = data["Attributes"]["mcs_phone"];
    this.mod.loginaccount.cardid = data["Attributes"]["mcs_cardid"];
    this.mod.loginaccount.email = data["Attributes"]["mcs_email"];
    this.mod.loginaccount.birthday = data["Attributes"]["mcs_birthday@OData.Community.Display.V1.FormattedValue"];
    this.mod.loginaccount.marriagestatus = data["Attributes"]["mcs_marriagestatus@OData.Community.Display.V1.FormattedValue"];
    this.mod.loginaccount.profession = data["Attributes"]["mcs_profession"];
    this.mod.loginaccount.company = data["Attributes"]["mcs_company"];
    this.mod.loginaccount.signature = data["Attributes"]["mcs_signature"];
    this.mod.loginaccount.description = data["Attributes"]["mcs_description"];
    this.mod.loginaccount.memberid = data["Attributes"]["_mcs_memberid_value"];
    this._logininfo.SetInfo(JSON.stringify(this.mod.loginaccount));
  }
  onReg() {
    this.title = '';
    this.disstatus = 3;
  }
  //
  onRegPost() {

    if (this.mod.model.name == '') {
      this._page.alert("", "");
      return false;
    }
    if (this.mod.model.pwd == '') {
      this._page.alert("", "");
      return false;
    }

    this._page.loadingShow();
    var postData = {
      account: this.mod.model.account,
      phone: this.mod.model.account,
      valcode: null,
      name: this.mod.model.name,
      nickname: this.mod.model.name,
      gender: null,
      mcs_cardid: null,
      mcs_email: null,
      birthday: null,
      marriagestatus: null,
      profession: null,
      company: null,
      signature: null,
      description: null,
      logintype: 1,
      userkey: {
        pwd: this.mod.model.pwd,
        keytype: 1,
        status: 2,
        certificationtype: 1
      }, quests: [
        {
          securityquestion: this.mod.model.quest1,
          answer: this.mod.model.quest1value,
        }, {
          securityquestion: this.mod.model.quest2,
          answer: this.mod.model.quest2value,
        }, {
          securityquestion: this.mod.model.quest3,
          answer: this.mod.model.quest3value,
        }
      ]
    };
    this._http.post(
      this.mod.regurl,
      postData,
      (res: any) => {
        if (res.Result == true) {
          var postData = {
            account: this.mod.model.account,
            valcode: "",
            logintype: 1,
            type: "",
            keytype: 1,
            status: 2,
            pwd: ""
          };
          //
          this._http.post(
            this.mod.getuserurl,
            postData,
            (res: any) => {
              this._page.loadingHide();
              if (res.Result == true) {
                this.LoginModel(res.Data);
                this.onReturn(true);
              }
              else {
                this.i = 0;
                this._page.alert("", res.Description);
              }
            },
            (err: any) => {
              this.i = 0;
              this._page.loadingHide();
              this._page.alert("", "");
            }
          );

        }
        else {
          this._page.alert("", "");
        }
      },
      (err: any) => {
        this._page.loadingHide();
        this._page.alert("", "");
      }
    );
  }
  ngOnInit() { }



  //
  onSendMsgPwd(status) {
    var ii = 180;
    $('#btnsendmsg').text('180s');
    $("#btnsendmsg").attr("class", "ion-color ion-color-medium  ios button button-block button-small button-solid ion-activatable ion-focusable hydrated");
    $('#btnsendmsg').attr('disabled', 'true');
    var interval = setInterval(function () {
      ii--;
      var cc = String(ii) + 's';
      $('#btnsendmsg').text(cc);
      if (ii == 0) {
        $('#btnsendmsg').text('');
        $("#btnsendmsg").attr("class", "ion-color ion-color-secondary ios button button-block button-small button-solid ion-activatable ion-focusable hydrated");
        $('#btnsendmsg').attr('disabled', 'false');
        clearInterval(interval);
      }
    }, 1000);

    this.onSendMsg(status);
  }
  //
  onPwdReset(type) {
    if (this.mod.model.account == '') {
      this.valmsg = '';
      this.isval = true;
      return false;
    }
    if (this.mod.model.msg == '') {
      this.valmsg = '';
      this.isval = true;
      return false;
    }
    if (this.mod.model.pwd != this.mod.model.pwd1) {
      this.valmsg = '';
      this.isval = true;
      return false;
    }

    this._page.loadingShow();
    var postData = {
      account: this.mod.model.account,
      valcode: this.mod.model.msg,
      logintype: 1,
      type: String(type),
      keytype: 1,
      status: 2,
      pwd: this.mod.model.pwd,
      certificationtype: 1
    };
    this._http.post(
      this.mod.resetpwdurl,
      postData,
      (res: any) => {
        if (res.Result == true) {
          this._page.loadingHide();
          this.disstatus = 7;
        }
        else {
          this._page.loadingHide();
          this._page.alert("", "");
        }
      },
      (err: any) => {
        this._page.loadingHide();
        this._page.alert("", "");
      }
    );
  }
}
